import React, { Component } from 'react'
import TimeDisplay from './TimeDisplay'
import LabsList from './LabsList'
import Button from './styles/Button'
import classes from './components/Chronometer/chronometer.module.scss'

class Chronometer extends Component {
  state = {
    isRunning: false,
    timerTime: 0,
    timerStart: 0,
    timerLabs: [],
  }

  onStart = () => {
    this.setState({
      isRunning: true,
      timerStart: Date.now() - this.state.timerTime,
    })
    this.timer = setInterval(() => {
      this.setState({
        timerTime: Date.now() - this.state.timerStart,
      })
    }, 10)
  }

  onStop = () => {
    this.setState({ isRunning: false })
    clearInterval(this.timer)
  }

  onReset = () => {
    this.setState({
      timerTime: 0,
      timerStart: 0,
      timerLabs: [],
    })
  }

  saveTime = (time) => {
    const timerLabs = [time, ...this.state.timerLabs]
    this.setState({ timerLabs })
  }

  render() {    
    return (
      <div className={classes.chronometer}>
        <TimeDisplay styles={classes.display} time={this.state.timerTime} />

        <div className="btn-group" style={{display: 'flex', justifyContent: 'space-between', marginBottom: '2rem'}}>
          {this.state.isRunning || this.state.timerTime === 0 ? (
            <Button
              disabled={this.state.timerTime === 0 || !this.state.isRunning}
              onClick={() => this.saveTime(this.state.timerTime)}
            >
              Lab
            </Button>
          ) : (
            <Button onClick={this.onReset}>Reset</Button>
          )}

          {this.state.isRunning ? (
            <Button onClick={this.onStop}>Stop</Button>
          ) : (
            <Button onClick={this.onStart}>Start</Button>
          )}
        </div>

        <LabsList styles={classes['lab-list']} labs={this.state.timerLabs} />
        <footer style={{ textAlign: 'center', marginTop: '1.25rem'}}>
          <p>Build with <a href="https://reactjs.org/">React</a>. This code is Open Source and available at <a href="https://github.com/AGutierrezR/chronometer-react">Github</a> </p>
        </footer>
      </div>
    )
  }
}

export default Chronometer
