import React from 'react'
import Lab from './components/Lab'

const LabsList = ({ labs, styles }) => {
  const totalLabs = labs.length
  return (
    <div className={styles}>
      {labs.map((lab, index) => {
        const labTime = index > 0 ? labs[index - 1] - lab : lab;
        return <Lab key={index} number={totalLabs - index} time={labTime} />
      })}
    </div>
  )
}

export default LabsList
